const config = {
  LightColor: [1.0, 1.0, 1.0],

  PropertyList: [
    {
      name: "translation",
      type: "vec3",
      value: [0, 0, 0]
    },
    {
      name: "isON",
      type: "bool",
      value: true
    },
    {
      name: "radius",
      type: "float",
      value: 0.5
    }
  ],

  InitList: [
    {
      name: "emissive_color",
      value: [1, 1, 1]
    }
  ]
};

export default config;



