const config = {
  LightColor: [1.0, 1.0, 1.0],

  PropertyList: [
    {
      name: "light_position",
      type: "vec3",
      value: [0, 0, 0]
    },
    {
      name: "light_color",
      type: "vec3",
      value: [1.0, 1.0, 1.0]
    }
  ]
};

export default config;



